
body {
    background: -webkit-gradient(linear, left top, right top, from(#829a8b), to(#4f345a));
    background: -webkit-linear-gradient(left, #829a8b, #4f345a);
    background: -o-linear-gradient(left, #829a8b, #4f345a);
    background: linear-gradient(to right, #829a8b, #4f345a);
    margin: 2%;
}
.App {
    display: -ms-flexbox;
    display: flex;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin: 2%;
}
.view {
    
    display: -ms-flexbox;
    
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 80%;
}
.nav-box {
    width: 20%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    width: 300px;
    height: 600px;
   
    
}
.nav-bar {
    font-family: 'Mina', sans-serif;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    border: 4px solid #9cbfa7;
    border-radius: 20px;
    width: 150px;
    height: inherit;
    
}
.nav-bar div {
    color: #c9f299;
}
.nav-button {
    border-radius: 15px;
    width:150px;
    height: 33%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}
.nav-line {
    border: 2px solid #829a8b;
    width: 80%;
}
.list-box {
    width: 150px;
    height: inherit;
}
.projects-list, .videos-list {
    height: inherit;
    width: inherit;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around; 
    -ms-flex-align: center; 
        align-items: center;
}
.project-thumb, .video-thumb {
    border: 2px solid #9cbfa7;
    border-radius: 10px;
    height: 18%;
    width: 98%;
    padding: 2%;
    width: 100px;
}

.header {
    height: 100px;
    font-family: 'Mina', sans-serif;
    position: relative;
}
.header h1 {
    color: #c9f299;
}
.logo1 {
    position: absolute;
    top: 0;
    left: 28%;

}
.logo2 {
    position: absolute;
    top: -1%;
    left: 32%;
    
}
.logo3 {
    position: absolute;
    top: -7%;
    left: 37%;
}
.logo4 {
    position: absolute;
    top: 2%;
    left: 39%;
}
.logo5 {
    position: absolute;
    top: 10%;
    left: 54.5%;
}
.logo6 {
    position: absolute;
    top: 0;
    left: 57%;
}
.logo7 {
    position: absolute;
    top: -3%;
    left: 60%;
}
.logo8 {
    position: absolute;
    top: 0;
    left: 68%;
}

.view-box {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    height: 500px;
}
.home-view {
    position: relative;
   
}
.home-view img {
    border: 2px solid #9cbfa7;
    border-radius: 10px;
}
.home-view p {
    font-family: 'Mina', sans-serif;
    font-size: 150%;
    font-weight: 600;
    color: #c9f299;
}

.broken-3 {
    position: absolute;
    top: 20%;
    left: 2%;
}
.broken-4 {
    position: absolute;
    top: 27%;
    left: 20%;
}
.broken-5 {
    position: absolute;
    top: 36%;
    left: 50%;
}
.broken-6 {
    position: absolute;
    top: 20%;
    left: 70%;
    
}
.projects-view {
    height: 100%;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
.projects-view h2 {
    font-family: 'Mina', sans-serif;
    color: #9cbfa7;
}
.projects-view h3 {
    font-family: 'Lato', sans-serif;
    color: #c9f299;
}
.bandcamp-embed {
    border: 4px solid #9cbfa7;
    border-radius: 10px;
    height: 100%;
    margin-right: 2%;
}
.projects-info {
    width: 40%;
}
.video-player {
    border: 4px solid #9cbfa7;
    border-radius: 5px;
}
/* hover states */
.nav-button:hover {
    background-color: #9cbfa7;
}
.project-thumb:hover, .video-thumb:hover {
    border: 4px solid #c9f299;
}
/* media queries  */
@media only screen and (max-width: 768px) {
    .App {
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-align: center;
            align-items: center;
    }
    .header h1 {
        font-size: 120%;
    }
    .view, .nav-box {
        width: 100%;
    }
    .nav-box {
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-pack: center;
            justify-content: center;
        
        
    }
    .nav-bar {
        -ms-flex-direction: row;
            flex-direction: row;
        width: 100%;
        height: 100px; 
    }
    .nav-button {
        height: 100px;
        width: 33%;
    }
    .nav-line {
        width: 0;
        height: 80%;
    }
    .list-box {
        height: 100px;
        width: 80%;
        margin-bottom: 8%;
        margin-top: 8%;
    }
    .projects-list, .videos-list {
        -ms-flex-direction: row;
            flex-direction: row;
        height: 100px;
    }
    .project-thumb, .video-thumb {
        height: 100%;
    }
    img, iframe {
        max-width: 75%;
        height: auto;
    }
    @media only screen and (max-width: 600px) {
        img {
            max-width: 75%;
            height: auto;
        }
    }
    

}


